.policy-page {
  padding: 2rem 0;
}

.page-title {
  text-align: center;
  margin-bottom: 2rem;
  color: var(--neon-red);
}

.policy-section {
  margin-bottom: 2.5rem;
}

.policy-section h2 {
  color: var(--neon-blue);
  margin-bottom: 1rem;
  font-size: 1.3rem;
}

.policy-section p {
  margin-bottom: 1rem;
  line-height: 1.6;
}

.policy-section ul {
  padding-left: 1.5rem;
  margin: 1rem 0;
}

.policy-section li {
  margin-bottom: 0.5rem;
}